.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 50px 40px;

  background: @main-color;
}

.form h2 {
  width: 200px;
  padding-left: 35px;
  margin-bottom: 15px;

  font-size: 14px;

  background: url("../images/bg_secureme_3.png") no-repeat;
  background-position: 0 5px;
  text-transform: uppercase;
}

.form input {
  width: 100%;
  padding: 7px 10px;
  margin-bottom: 7px;
}

.form select {
  width: 100%;
  padding: 7px 10px;
  margin-bottom: 15px;
}

.form__btn {
  align-self: flex-end;
  padding: 10px 20px;

  border: none;
  background: #faf7c4;

  cursor: pointer;

  &:hover {
    background: #f1ee83;
  }
}

@media (max-width: 1100px) {
  .main-form {
    margin-bottom: 20px;
  }
}
