.warn {
  margin-bottom: 20px;
}

.warn__wrapper {
  padding: 0 60px;
}

.warn__main-info {
  display: flex;
  padding-bottom: 20px;

  border-bottom: 5px solid @main-color;
}

.warn__main-info p {
  margin-left: 40px;
  font-size: 24px;
  font-weight: 800;
  color: @main-color;
}

.warn__info {
  padding: 20px 0;

  font-size: 20px;
  color: @main-color;
  font-weight: 800;
  text-align: justify;
}

@media (max-width: 1100px) {
  .warn__wrapper {
    padding: 0;
  }

  .warn__main-info {
    flex-direction: column;
    align-items: center;
  }

  .warn__img {
    margin-bottom: 20px;
  }

  .warn__main-info p {
    margin: 0;
  }
}
