.price {
  margin-bottom: 40px;
}

.price__title {
  margin-bottom: 20px;
}

.price__table {
  margin-bottom: 20px;
}

.price__table td {
  padding: 15px  10px;

  background: @grey;
  text-align: center;
}

.price__main-head td{
  background: @main-color;
}

.price__head td {
  background: @yellow;
}

.price__btn {
  width: 180px;
  padding: 12px;

  background: @yellow url("../images/bg_download.png") no-repeat 11px;
  border: none;
  text-align: right;
  cursor: pointer;

  &:hover {
    background-color: darken(@yellow, 10%);
  }
}
