.document {
  width: 100%;
}

.document__title {
  margin-bottom: 20px;
}

.document span {
  display: block;
  margin-bottom: 20px;
  color: @main-color;
}

.document__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.document__name {
  width: 100%;
  height: 40px;
  padding: 10px;
  background: @grey;
}

.document__btn {
  width: 40px;
  height: 40px;
  margin-left: 10px;

  background: @yellow url("../images/bg_download.png") no-repeat center;
  border: none;
  cursor: pointer;

  &:hover {
    background-color: darken(@yellow, 10%);
  }
}
