.news {
  padding: 20px;
  margin-bottom: 40px;

  background: @grey;
}

.news__wrapper {
  display: flex;
}

.news__info {
  padding-top: 20px;
  margin-left: 40px;
}

.news__title {
  margin-bottom: 30px;
  font-size: 32px;
}

.news__list {
  display: flex;
}

.news__item {
  width: 350px;
  margin-right: 40px;
}

.news__item b {
  display: block;
  margin-bottom: 15px;

  font-size: 18px;
}

.news__item p {
  margin-bottom: 30px;
}

@media (max-width: 1100px) {
  .news__wrapper {
    flex-direction: column;
  }
}
