.address {
  margin-bottom: 40px;
}

.address__btn-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.address__btn-container label {
  width: 350px;
  padding: 13px;

  text-align: center;
  cursor: pointer;

  transition: 0.3s;

  background: @grey;
  border: 1px solid darken(@grey, 5%);

  &:hover {
    background: darken(@grey, 5%);
  }
}

.address__btn-scheme:checked ~ .address__btn-container label[for="btn-scheme"] {
  background: #faf7c4;
  border: 1px solid darken(#faf7c4, 15%);

  &:hover {
    background: darken(#faf7c4, 15%);
  }
}

.address__btn-back-call:checked ~ .address__btn-container label[for="btn-back-call"] {
  background: #faf7c4;
  border: 1px solid darken(#faf7c4, 15%);

  &:hover {
    background: darken(#faf7c4, 15%);
  }
}

.address__btn-back-call:checked ~ .address__map-and-back-call .address__map {
  margin-top: -350px;
}

.address__map-and-back-call {
  display: flex;
  flex-direction: column;
  height: 350px;

  overflow: hidden;
}

.address__map {
  transition: 0.5s;
}

.address__back-call {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 350px;
  padding: 30px;

  background:  @grey;
}

.address__back-call input {
  width: 250px;
  padding: 8px 10px;
  margin-bottom: 10px;
}

.address__back-call textarea {
  width: 250px;
  height: 70px;
  padding: 8px 10px;
  margin-bottom: 10px;

  resize: none;
}

.address__back-call span {
  font-size: 12px;
}

.address__btn-submit {
  width: 160px;
  height: 40px;
  align-self: flex-end;

  font-size: 14px;
  font-weight: 800;
  text-transform: uppercase;

  background: #faf7c4;
  border: none;

  cursor: pointer;

  &:hover {
    background: darken(#faf7c4, 15%);
  }
}
