@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
body {
  margin: 0;
  box-sizing: border-box;
}
* {
  font-family: "Roboto", sans-serif;
  box-sizing: inherit;
}
a {
  text-decoration: none;
}
ul,
p,
h1,
h2,
h3 {
  list-style: none;
  margin: 0;
  padding: 0;
}
.hidden {
  display: none;
}
.wrapper {
  max-width: 1100px;
  margin: 0 auto;
}
.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
.main {
  display: flex;
  justify-content: space-between;
}
.main__left {
  width: 100%;
  padding-left: 60px;
  padding-right: 40px;
  margin-bottom: 20px;
}
@media (max-width: 1100px) {
  .wrapper {
    max-width: 768px;
  }
  .main {
    flex-direction: column;
  }
  .main__left {
    order: 2;
    padding: 0;
  }
}
.button {
  padding: 12px 25px;
  color: white;
  background-color: #1182c5;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}
.button:hover {
  opacity: 0.85;
}
.head {
  background-color: #1182c5;
}
.head__wrapper {
  display: flex;
  padding-top: 20px;
  padding-bottom: 5px;
}
.head__phones {
  display: flex;
  flex-direction: column;
  padding: 5px 17px 5px 48px;
  margin-bottom: 40px;
  background: url("../images/bg_callme.png") no-repeat 2px 1px;
  border: 1px solid black;
  border-top-left-radius: 13% 50%;
  border-bottom-left-radius: 13% 50%;
  border-top-right-radius: 13% 50%;
  border-bottom-right-radius: 13% 50%;
}
.head__phones span {
  color: #ffffff;
}
.head__list {
  display: flex;
  align-self: flex-end;
  margin-left: 80px;
}
.head__item a {
  display: block;
  padding: 10px 15px;
  font-size: 16px;
  color: #ffffff;
  transition: 0.3s;
}
.head__item a:hover {
  background: #0d6396;
}
@media (max-width: 1100px) {
  .head__list {
    flex-wrap: wrap;
    width: 390px;
  }
  .head__item {
    width: 130px;
  }
}
.current {
  background: #ebebeb;
  margin-bottom: 40px;
}
.current h1 {
  padding: 30px 0 10px 60px;
  font-size: 34px;
}
@media (max-width: 1100px) {
  .current h1 {
    padding-left: 0;
  }
}
.sidebar__service {
  padding: 30px;
  margin-bottom: 20px;
  background: #faf7c4;
}
.sidebar__title {
  padding-left: 30px;
  margin-bottom: 15px;
  font-size: 20px;
  color: #1182c5;
  background: url("../images/bg_shield.png") no-repeat;
}
.sidebar__item {
  margin-left: 20px;
  margin-bottom: 5px;
  color: #1182c5;
  list-style-type: disc;
}
.sidebar__item a {
  color: #1182c5;
}
.sidebar__item a:hover {
  color: #0d6396;
}
.sidebar__main-service {
  display: flex;
  justify-content: space-between;
}
.sidebar__main-service a {
  display: block;
  width: 55px;
  height: 55px;
  margin-bottom: 20px;
  background-color: #faf7c4;
  background-repeat: no-repeat;
  background-position: -6px -7px;
}
.sidebar__main-service a:hover {
  background-color: #f6f195;
}
.sidebar__main-service--cottage {
  background-image: url("../images/bg_home.png");
}
.sidebar__main-service--flat {
  background-image: url("../images/bg_flat.png");
}
.sidebar__main-service--object {
  background-image: url("../images/bg_factory.png");
}
.sidebar__main-service--storage {
  background-image: url("../images/bg_storage.png");
}
@media (max-width: 1100px) {
  .sidebar {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-bottom: 40px;
    order: 1;
  }
  .sidebar__form {
    width: 300px;
  }
  .sidebar__service {
    width: 300px;
  }
  .sidebar__service h2 {
    margin-bottom: 20px;
  }
  .sidebar__item {
    margin-bottom: 10px;
  }
  .sidebar__item a {
    font-size: 17px;
  }
  .sidebar__main-service {
    flex-direction: column;
  }
  .sidebar__main-service a {
    width: 80px;
    height: 80px;
    background-position: 5px 5px;
  }
}
.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 50px 40px;
  background: #1182c5;
}
.form h2 {
  width: 200px;
  padding-left: 35px;
  margin-bottom: 15px;
  font-size: 14px;
  background: url("../images/bg_secureme_3.png") no-repeat;
  background-position: 0 5px;
  text-transform: uppercase;
}
.form input {
  width: 100%;
  padding: 7px 10px;
  margin-bottom: 7px;
}
.form select {
  width: 100%;
  padding: 7px 10px;
  margin-bottom: 15px;
}
.form__btn {
  align-self: flex-end;
  padding: 10px 20px;
  border: none;
  background: #faf7c4;
  cursor: pointer;
}
.form__btn:hover {
  background: #f1ee83;
}
@media (max-width: 1100px) {
  .main-form {
    margin-bottom: 20px;
  }
}
.news {
  padding: 20px;
  margin-bottom: 40px;
  background: #ebebeb;
}
.news__wrapper {
  display: flex;
}
.news__info {
  padding-top: 20px;
  margin-left: 40px;
}
.news__title {
  margin-bottom: 30px;
  font-size: 32px;
}
.news__list {
  display: flex;
}
.news__item {
  width: 350px;
  margin-right: 40px;
}
.news__item b {
  display: block;
  margin-bottom: 15px;
  font-size: 18px;
}
.news__item p {
  margin-bottom: 30px;
}
@media (max-width: 1100px) {
  .news__wrapper {
    flex-direction: column;
  }
}
.warn {
  margin-bottom: 20px;
}
.warn__wrapper {
  padding: 0 60px;
}
.warn__main-info {
  display: flex;
  padding-bottom: 20px;
  border-bottom: 5px solid #1182c5;
}
.warn__main-info p {
  margin-left: 40px;
  font-size: 24px;
  font-weight: 800;
  color: #1182c5;
}
.warn__info {
  padding: 20px 0;
  font-size: 20px;
  color: #1182c5;
  font-weight: 800;
  text-align: justify;
}
@media (max-width: 1100px) {
  .warn__wrapper {
    padding: 0;
  }
  .warn__main-info {
    flex-direction: column;
    align-items: center;
  }
  .warn__img {
    margin-bottom: 20px;
  }
  .warn__main-info p {
    margin: 0;
  }
}
.info {
  margin-bottom: 60px;
}
.info__wrapper {
  padding: 0 60px;
}
.info__list {
  display: flex;
  flex-wrap: wrap;
}
.info__item {
  position: relative;
  width: 50%;
  padding: 0 40px;
}
.info__item:first-letter {
  padding-left: 15px;
}
.info__item::before {
  content: "";
  position: absolute;
  top: 9px;
  left: 44px;
  width: 3px;
  height: 3px;
  background: #1182c5;
}
.info__item span {
  font-weight: 700;
}
@media (max-width: 1100px) {
  .info__wrapper {
    padding: 0;
  }
}
.ourCustomers {
  background: #f3f3f3;
}
.ourCustomers__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0;
}
.ourCustomers__title {
  margin-bottom: 20px;
  font-size: 30px;
}
.ourCustomers__slider {
  display: flex;
  align-items: center;
}
.ourCustomers__list {
  display: flex;
  width: 960px;
  overflow: hidden;
}
.ourCustomers__item {
  padding: 10px;
  width: 240px;
  height: 120px;
  flex-shrink: 0;
  transition: 0.3s;
}
.ourCustomers__item:hover {
  background: #dfdfdf;
}
.ourCustomers__item a {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.ourCustomers__item img {
  width: auto;
  height: 100%;
}
.ourCustomers__btn {
  position: relative;
  width: 50px;
  height: 120px;
  background: transparent;
  border: none;
  cursor: pointer;
}
.ourCustomers__btn::before {
  content: "";
  position: absolute;
  top: 44px;
  left: 0;
  width: 30px;
  height: 30px;
  border-top: 3px solid black;
  border-right: 3px solid black;
  transform: rotate(45deg);
}
.ourCustomers__btn:hover {
  background: #ebebeb;
}
.ourCustomers__btn-left::before {
  left: 13px;
  transform: rotate(-135deg);
}
@media (max-width: 1100px) {
  .ourCustomers__list {
    width: 720px;
  }
}
.service {
  margin-bottom: 40px;
}
.service b {
  display: flex;
  justify-content: center;
  padding-bottom: 300px;
  margin-bottom: 10px;
  font-size: 20px;
  background: url("../images/house3.png") no-repeat center bottom;
}
.service p {
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 18px;
}
.service__item {
  margin-bottom: 10px;
}
.service__item:last-child {
  margin-bottom: 20px;
}
.service__item a {
  display: block;
  padding: 15px 20px;
  color: black;
  border: 1px solid #0d6396;
  border-radius: 10px;
  transition: 0.2s;
}
.service__item a:hover {
  color: #ffffff;
  background: #1d9fec;
}
.service .service__question {
  font-weight: 400;
}
.equipment {
  margin-bottom: 40px;
}
.equipment__title {
  margin-bottom: 30px;
}
.equipment__list {
  display: flex;
  flex-wrap: wrap;
}
.equipment__item {
  width: 220px;
  margin-right: 30px;
  margin-bottom: 20px;
  border: 2px solid #0d6396;
}
.equipment__item:nth-child(3n) {
  margin-right: 0;
}
.equipment__img img {
  width: 100%;
  background: grey;
}
.equipment__name {
  display: flex;
  height: 90px;
  padding: 10px 15px;
  background: #1182c5;
}
@media (max-width: 1100px) {
  .equipment__list {
    padding: 0;
  }
}
.contact {
  margin-bottom: 40px;
}
.contact p {
  margin-bottom: 20px;
}
.contact__title {
  margin-bottom: 20px;
}
.contact__info {
  font-size: 17px;
  color: #0d6396;
}
.contact__address {
  display: flex;
  flex-direction: column;
}
.contact__phone {
  display: flex;
  flex-direction: column;
}
.contact__graph {
  display: flex;
  flex-direction: column;
}
.contact__graph b {
  margin-bottom: 10px;
}
.address {
  margin-bottom: 40px;
}
.address__btn-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.address__btn-container label {
  width: 350px;
  padding: 13px;
  text-align: center;
  cursor: pointer;
  transition: 0.3s;
  background: #ebebeb;
  border: 1px solid #dfdfdf;
}
.address__btn-container label:hover {
  background: #dfdfdf;
}
.address__btn-scheme:checked ~ .address__btn-container label[for="btn-scheme"] {
  background: #faf7c4;
  border: 1px solid #f4ed7d;
}
.address__btn-scheme:checked ~ .address__btn-container label[for="btn-scheme"]:hover {
  background: #f4ed7d;
}
.address__btn-back-call:checked ~ .address__btn-container label[for="btn-back-call"] {
  background: #faf7c4;
  border: 1px solid #f4ed7d;
}
.address__btn-back-call:checked ~ .address__btn-container label[for="btn-back-call"]:hover {
  background: #f4ed7d;
}
.address__btn-back-call:checked ~ .address__map-and-back-call .address__map {
  margin-top: -350px;
}
.address__map-and-back-call {
  display: flex;
  flex-direction: column;
  height: 350px;
  overflow: hidden;
}
.address__map {
  transition: 0.5s;
}
.address__back-call {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 350px;
  padding: 30px;
  background: #ebebeb;
}
.address__back-call input {
  width: 250px;
  padding: 8px 10px;
  margin-bottom: 10px;
}
.address__back-call textarea {
  width: 250px;
  height: 70px;
  padding: 8px 10px;
  margin-bottom: 10px;
  resize: none;
}
.address__back-call span {
  font-size: 12px;
}
.address__btn-submit {
  width: 160px;
  height: 40px;
  align-self: flex-end;
  font-size: 14px;
  font-weight: 800;
  text-transform: uppercase;
  background: #faf7c4;
  border: none;
  cursor: pointer;
}
.address__btn-submit:hover {
  background: #f4ed7d;
}
.price {
  margin-bottom: 40px;
}
.price__title {
  margin-bottom: 20px;
}
.price__table {
  margin-bottom: 20px;
}
.price__table td {
  padding: 15px  10px;
  background: #ebebeb;
  text-align: center;
}
.price__main-head td {
  background: #1182c5;
}
.price__head td {
  background: #faf7c4;
}
.price__btn {
  width: 180px;
  padding: 12px;
  background: #faf7c4 url("../images/bg_download.png") no-repeat 11px;
  border: none;
  text-align: right;
  cursor: pointer;
}
.price__btn:hover {
  background-color: #f6f195;
}
.document {
  width: 100%;
}
.document__title {
  margin-bottom: 20px;
}
.document span {
  display: block;
  margin-bottom: 20px;
  color: #1182c5;
}
.document__item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.document__name {
  width: 100%;
  height: 40px;
  padding: 10px;
  background: #ebebeb;
}
.document__btn {
  width: 40px;
  height: 40px;
  margin-left: 10px;
  background: #faf7c4 url("../images/bg_download.png") no-repeat center;
  border: none;
  cursor: pointer;
}
.document__btn:hover {
  background-color: #f6f195;
}
.foot {
  background: #0b547f;
}
.foot__wrapper {
  display: flex;
  padding: 20px 0;
  background: url("../images/bg_footer.png") no-repeat center;
  background-size: 20%;
}
.foot__menu-left h2 {
  margin-bottom: 10px;
  font-size: 20px;
  color: #f1ee83;
}
.foot__menu-left a {
  color: #faf7c4;
  font-size: 14px;
  transition: 0.2s;
}
.foot__menu-left a:hover {
  color: #f0e74e;
}
.foot__menu-center {
  margin-left: 40px;
}
.foot__menu-center h2 {
  margin-bottom: 10px;
  font-size: 20px;
  color: #f1ee83;
}
.foot__menu-center a {
  color: #faf7c4;
  font-size: 14px;
  transition: 0.2s;
}
.foot__menu-center a:hover {
  color: #f0e74e;
}
.foot__menu-right {
  margin-left: auto;
}
.foot__contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.foot__contacts {
  color: #faf7c4;
}
.foot__description {
  width: 300px;
  text-align: right;
  color: #faf7c4;
}
@media (max-width: 1100px) {
  .foot__wrapper {
    background-position: 315px;
  }
}
