body {
  margin: 0;
  box-sizing: border-box;
}

* {
  font-family: "Roboto", sans-serif;

  box-sizing: inherit;
}

a {
  text-decoration: none;
}

ul,
p,
h1,
h2,
h3 {
  list-style: none;
  margin: 0;
  padding: 0;
}
.hidden {
  display: none;
}

.wrapper {
  max-width: 1100px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.main {
  display: flex;
  justify-content: space-between;
}

.main__left {
  width: 100%;
  padding-left: 60px;
  padding-right: 40px;
  margin-bottom: 20px;
}

@media (max-width: 1100px) {
  .wrapper {
    max-width: 768px;
  }

  .main {
    flex-direction: column;
  }

  .main__left {
    order: 2;
    padding: 0;
  }
}
