.ourCustomers {
  background: #f3f3f3;
}

.ourCustomers__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0;
}

.ourCustomers__title {
  margin-bottom: 20px;

  font-size: 30px;
}

.ourCustomers__slider {
  display: flex;
  align-items: center;
}

.ourCustomers__list {
  display: flex;
  width: 960px;

  overflow: hidden;
}

.ourCustomers__item {
  padding: 10px;
  width: 240px;
  height: 120px;
  flex-shrink: 0;

  transition: 0.3s;

  &:hover {
    background: darken(@grey, 5%);
  }
}

.ourCustomers__item a {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.ourCustomers__item img {
  width: auto;
  height: 100%;
}

.ourCustomers__btn {
  position: relative;

  width: 50px;
  height: 120px;

  background: transparent;
  border: none;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 44px;
    left: 0;

    width: 30px;
    height: 30px;

    border-top: 3px solid black;
    border-right: 3px solid black;
    transform: rotate(45deg);
  }

  &:hover {
    background: @grey;
  }
}

.ourCustomers__btn-left::before {
  left: 13px;
  transform: rotate(-135deg);
}

@media (max-width: 1100px) {
  .ourCustomers__list {
    width: 720px;
  }
}
