.head {
  background-color: @main-color;
}

.head__wrapper {
  display: flex;
  padding-top: 20px;
  padding-bottom: 5px;
}

.head__phones {
  display: flex;
  flex-direction: column;
  padding: 5px 17px 5px 48px;
  margin-bottom: 40px;

  background: url("../images/bg_callme.png") no-repeat 2px 1px;
  border: 1px solid black;
  border-top-left-radius: 13% 50%;
  border-bottom-left-radius: 13% 50%;
  border-top-right-radius: 13% 50%;
  border-bottom-right-radius: 13% 50%;
}

.head__phones span {
  color: #ffffff;
}

.head__list {
  display: flex;
  align-self: flex-end;
  margin-left: 80px;
}

.head__item a {
  display: block;
  padding: 10px 15px;

  font-size: 16px;
  color: #ffffff;

  transition: 0.3s;

  &:hover {
    background: @hover-color;
  }
}

@media (max-width: 1100px) {
  .head__list {
    flex-wrap: wrap;
    width: 390px;
  }

  .head__item {
    width: 130px;
  }
}
