.foot {
  background: darken(@main-color, 15%);
}

.foot__wrapper {
  display: flex;
  padding: 20px 0;

  background: url("../images/bg_footer.png") no-repeat center;
  background-size: 20%;
}

.foot__menu-left h2 {
  margin-bottom: 10px;

  font-size: 20px;
  color: #f1ee83;
}

.foot__menu-left a {
  color: #faf7c4;
  font-size: 14px;

  transition: 0.2s;

  &:hover {
    color: darken(#faf7c4, 25%);
  }
}

.foot__menu-center {
  margin-left: 40px;
}

.foot__menu-center h2 {
  margin-bottom: 10px;

  font-size: 20px;
  color: #f1ee83;
}

.foot__menu-center a {
  color: #faf7c4;
  font-size: 14px;

  transition: 0.2s;

  &:hover {
    color: darken(#faf7c4, 25%);
  }
}

.foot__menu-right {
  margin-left: auto;
}

.foot__contacts {
  display: flex;
  flex-direction: column;
  align-items:  flex-end;
}

.foot__contacts {
  color: #faf7c4;
}

.foot__description {
  width: 300px;
  text-align: right;
  color: #faf7c4;
}

@media (max-width: 1100px) {
  .foot__wrapper {
    background-position: 315px;
  }
}
