.sidebar__service {
  padding: 30px;
  margin-bottom: 20px;

  background: #faf7c4;
}

.sidebar__title {
  padding-left: 30px;
  margin-bottom: 15px;
  font-size: 20px;
  color: @main-color;

  background: url("../images/bg_shield.png")  no-repeat;
}

.sidebar__item {
  margin-left: 20px;
  margin-bottom: 5px;

  color: @main-color;
  list-style-type: disc;
}

.sidebar__item a {
  color: @main-color;

  &:hover {
    color: @hover-color;
  }
}

.sidebar__main-service {
  display: flex;
  justify-content: space-between;
}

.sidebar__main-service a {
  display: block;
  width: 55px;
  height: 55px;
  margin-bottom: 20px;

  background-color: #faf7c4;
  background-repeat: no-repeat;
  background-position: -6px -7px;

  &:hover {
    background-color: darken(#faf7c4, 10%);
  }
}

.sidebar__main-service--cottage {
  background-image: url("../images/bg_home.png");
}

.sidebar__main-service--flat {
  background-image: url("../images/bg_flat.png");
}

.sidebar__main-service--object {
  background-image: url("../images/bg_factory.png");
}

.sidebar__main-service--storage {
  background-image: url("../images/bg_storage.png");
}

@media (max-width: 1100px) {
  .sidebar {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-bottom: 40px;

    order: 1;
  }

  .sidebar__form {
    width: 300px;
  }

  .sidebar__service {
    width: 300px;
  }

  .sidebar__service h2 {
    margin-bottom: 20px;
  }

  .sidebar__item {
    margin-bottom: 10px;
  }

  .sidebar__item a {
    font-size: 17px;
  }

  .sidebar__main-service {
    flex-direction: column;
  }

  .sidebar__main-service a {
    width: 80px;
    height: 80px;

    background-position: 5px 5px;
  }
}
