.service {
  margin-bottom: 40px;
}

.service b {
  display: flex;
  justify-content: center;
  padding-bottom: 300px;
  margin-bottom: 10px;

  font-size: 20px;

  background: url("../images/house3.png") no-repeat center bottom;
}

.service p {
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 18px;
}

.service__item {
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 20px;
  }
}

.service__item a {
  display: block;
  padding: 15px 20px;

  color: black;

  border: 1px solid @hover-color;
  border-radius: 10px;

  transition: 0.2s;

  &:hover {
    color: #ffffff;

    background: lighten(@main-color, 10%);
  }
}

.service .service__question {
  font-weight: 400;
}
