.info {
  margin-bottom: 60px;
}

.info__wrapper {
  padding: 0 60px;
}

.info__list {
  display: flex;
  flex-wrap: wrap;
}

.info__item {
  position: relative;

  width: 50%;
  padding:  0 40px;

  &:first-letter {
    padding-left: 15px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 9px;
    left: 44px;

    width: 3px;
    height: 3px;

    background: @main-color;
  }
}

.info__item span {
  font-weight: 700;
}

@media (max-width: 1100px) {
  .info__wrapper {
    padding: 0;
  }
}
