.equipment {
  margin-bottom: 40px;
}

.equipment__title {
  margin-bottom: 30px;
}

.equipment__list {
  display: flex;
  flex-wrap: wrap;
}

.equipment__item {
  width: 220px;
  margin-right: 30px;
  margin-bottom: 20px;

  border: 2px solid @hover-color;

  &:nth-child(3n) {
    margin-right: 0;
  }
}

.equipment__img img {
  width: 100%;
  background: grey;
}

.equipment__name {
  display: flex;
  height: 90px;
  padding: 10px 15px;
  background: @main-color;
}

@media (max-width: 1100px) {
  .equipment__list {
    padding: 0;
  }
}
